import React, { Component } from 'react';

export class Tag extends Component {
    static displayName = Tag.name;

    constructor(props) {
        super(props);
    }

    render() {
        var style = { color: "red" }
        return (
            <span class="badge badge-secondary">ㅤ{this.props.children}ㅤ</span>
        );
    }
}
